* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
:root {
  --primary: #03a9f4;
  --dark: rgb(10, 25, 41);
  --light: #c8cbdb;
}
li {
  list-style: none;
}
a {
  text-decoration: none;
}
body {
  font-family: 'Noto Sans', sans-serif;
  background-color: var(--dark);
}
/* End Global Style */
.App {
  display: flex;
}
.navbar {
  min-width: 25%;
  min-height: 100vh;
  border-right: #888 solid 0.5px;
  position: fixed;
}
.navbar h1 {
  color: var(--primary);
  padding: 20px 25px;
  text-align: center;
  font-size: 35px;
  min-width: 100%;
  border-bottom: solid 1px #777;
}
.navbar .headers {
  width: 100%;
  height: 300px;
  padding: 10px;
  display: flex;
  align-items: center;
  align-content: center;
  flex-direction: column;
  justify-content: space-between;
  font-size: 24px;
}
.headers a {
  width: 100%;
  height: 100px;
  line-height: 3.5;
  transition: 0.25s;
  cursor: pointer;
  color: var(--light);
    font-weight: 400;
}
.headers a:hover {
  opacity: 0.5;
}
.headers a:not(:first-child),
.headers li:not(:first-child) {
  border-top: 1px solid #7777;
}

.content {
  margin: 10vh 5vw 5vh 30vw;
  width: 70vw;
  height: auto;
  border-radius: 5vh;
  background-color: rgb(16, 61, 108);
  padding: 5vh;
}
.content .home h2 {
  color: var(--light);
  padding: 0 0 20px 0;
  border-bottom: 1px solid #888;
}
.content .home .blogs-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  overflow: hidden;
}
article {
  color: var(--light);
}
article header {
  padding-bottom: 10px;
  margin-bottom: 10px;
  border-bottom: solid 1px #777;
}
article header h2 {
  font-size: 13px;
  margin-top: 5px;
}
article header span {
  color: var(--primary);
  font-weight: 700;
  font-size: 16px;
}
article section {
  font-size: 18px;
  line-height: 2.5;
}
.content .home .blogs-container .blog {
  padding: 10px;
  background-color: var(--dark);
  color: var(--light);
  border-radius: 20px;
  width: 80%;
  margin-top: 20px;
  cursor: pointer;
  transition: 0.25s;
  position: relative;
}
.content .home .blogs-container .blog:hover {
  opacity: 0.8;
}
.content .home .blogs-container .blog h4 {
  padding: 10px;
  border-bottom: 1px solid #888;
  font-size: 24px;
}
.content .home .blogs-container .blog p {
  padding: 10px 0 0 10px;
  font-size: 12px;
}
.blog .author span {
  color: var(--primary);
  font-weight: bold;
  font-size: 16px;
}
.blog a {
  color: var(--light);
}
.delete {
  padding: 12px;
  color: var(--dark);
  background-color: var(--primary);
  border: none;
  border-radius: 15px;
  cursor: pointer;
  transition: 0.4s;
  font-weight: bold;
  font-size: 18px;
  margin-top: 10px;
  float: right;
}
.delete:hover {
  color: #fff;
  background-color: #f00;
}
.load-msg,
.error-msg {
  color: var(--light);
  font-size: 24px;
  font-weight: 700;
}
.error-msg {
  color: rgba(250, 126, 126, 0.829);
}
.about {
  color: var(--light);
}
.about h1 {
  color: #fff;
}
.about div:not(:last-child) {
  margin-bottom: 20px;
  border-bottom: solid 1px #777;
  padding: 0 0 10px 0;
}
.create h1 {
  margin-bottom: 10px;
  color: var(--light);
  text-align: center;
}
form {
  display: flex;
  height: 70vh;
  justify-content: space-between;
  flex-direction: column;
  align-items: center;
  align-content: center;
}
form input,
textarea {
  background-color: var(--dark);
  border: none;
  padding: 15px;
  color: var(--light);
  margin-bottom: 10px;
  border-radius: 20px;
  font-size: 18px;
  width: 100%;
}
form input::placeholder,
textarea::placeholder {
  color: #fff;
}
form input:focus,
textarea:focus {
  outline: none;
  border: solid 1px var(--primary);
}
textarea {
  height: auto;
  flex: 1;
}
input[type="submit"] {
  width: 100px;
  float: right;
  margin-bottom: 0;
  cursor: pointer;
  transition-duration: 0.4s;
}
input[type="submit"]:hover {
  opacity: 0.65;
}
.done {
  color: var(--light);
  font-size: 50px;
}